<template>
    <div class="body">
      <div class="text" v-html="getInfo"></div>
      <div class="back"><span class="iconfont icon-fanhui" @click="$router.push('/')"></span></div>
    </div>
</template>

<script>
import resource from '../assets/resource'

export default {
  name: 'english-info',
  data () {
    return {
    }
  },
  computed: {
    getId: function () {
      return this.$route.params.id
    },
    getResource: function () {
      return resource
    },
    getInfo: function () {
      return this.getResource[this.getId]
    }
  }
}
</script>

<style scoped>
  .text {
    padding-top: 50px;
  }
  .back {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    height: 30px;
    padding: 10px 10px;
    background-color: blueviolet;
    font-weight: 600;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, .5);
  }
  .back span {
    color: white;
    font-size: 30px;
  }
  .back span:active {
    font-size: 35px;
    color: beige;
  }
</style>



